*,
*:after,
*:before {
    box-sizing: border-box;
}
.switch input {
/* First, we make it as wide as the container */
position: absolute;
width: 100%;
height: 100%;
/* Then, we put it on top of everything else */
z-index: 100;
/* Last, we make it invisible */
opacity: 0;
/* This one is just for ergonomy */
cursor: pointer;
}
.switch {
width: 180px;
height: 50px;
position: relative;
}
.switch label {
display: block;
width: 100%;
height: 100%;
position: relative;
background: #a5a39d;
border-radius: 40px;
box-shadow:
    inset 0 3px 8px 1px rgba(0,0,0,0.2),
    0 1px 0 rgba(255,255,255,0.5);
}
.switch label:after {
content: "";
position: absolute;
z-index: -1;
top: -8px; right: -8px; bottom: -8px; left: -8px;
border-radius: inherit;
background: #ccc; /* Fallback */
background: -webkit-gradient(linear, left top, left bottom, from(#f2f2f2), to(#ababab));
background: linear-gradient(#f2f2f2, #ababab);
box-shadow: 0 0 10px rgba(0,0,0,0.3),
    0 1px 1px rgba(0,0,0,0.25);
}
.switch label:before {
content: "";
position: absolute;
z-index: -1;
top: -18px; right: -18px; bottom: -18px; left: -18px;
border-radius: inherit;
background: #eee; /* Fallback */
background: -webkit-gradient(linear, left top, left bottom, from(#e5e7e6), to(#eee));
background: linear-gradient(#e5e7e6, #eee);
box-shadow: 0 1px 0 rgba(255,255,255,0.5);
-webkit-filter: blur(1px); /* Smooth trick */
filter: blur(1px); /* Future-proof */
}
.switch label i {
display: block;
height: 100%;
width: 60%;
position: absolute;
left: 0;
top: 0;
z-index: 2;
border-radius: inherit;
background: #b2ac9e; /* Fallback */
background: -webkit-gradient(linear, left top, left bottom, from(#f7f2f6), to(#b2ac9e));
background: linear-gradient(#f7f2f6, #b2ac9e);
box-shadow:
    inset 0 1px 0 white,
    0 0 8px rgba(0,0,0,0.3),
    0 5px 5px rgba(0,0,0,0.2);
}
.switch label i:after {
content: "";
position: absolute;
left: 15%;
top: 25%;
width: 70%;
height: 50%;
background: #d2cbc3; /* Fallback */
background: -webkit-gradient(linear, left top, left bottom, from(#cbc7bc), to(#d2cbc3));
background: linear-gradient(#cbc7bc, #d2cbc3);
border-radius: inherit;
}
.switch label i:before {
content: "off";
position: absolute;
top: 50%;
right: -50%;
margin-top: -12px;
color: #666; /* Fallback */
color: rgba(0,0,0,0.4);
font-style: normal;
font-weight: bold;
font-family: Helvetica, Arial, sans-serif;
font-size: 24px;
text-transform: uppercase;
text-shadow: 0 1px 0 #bcb8ae, 0 -1px 0 #97958e;
}
.switch input:checked ~ label { /* Background */
background: #9abb82;
}

.switch input:checked ~ label i { /* Toggle */
left: auto;
right: -1%;
}

.switch input:checked ~ label i:before { /* On/off */
content: "on";
right: 115%;
color: #82a06a;
text-shadow: 0 1px 0 #afcb9b, 0 -1px 0 #6b8659;
}

